import React from "react";
import styled from "styled-components";

import { DesktopPage } from "./DesktopPage";

const DesktopContainerStyler = styled.div`
  display: flex;
  flex: 1;
  flex-direction: column;
  background-image: url(https://i.pinimg.com/originals/0a/67/c0/0a67c056c4f360eaba4950bdd9092ab5.png);
  background-size: cover;
`;

export const DesktopContainer = () => {
  return (
    <DesktopContainerStyler>
      <DesktopPage />
    </DesktopContainerStyler>
  );
};
